/* global jQuery Vimeo */


(function ($) {

	"use strict";

	// video overlay hide on play

	$(".section17 .play-icon").on("click", function(){
		var poster = document.querySelector(".section17 .poster");
		var playIcon = document.querySelector(".section17 .play-icon");
		var playOverlay = document.querySelector(".section17 .q-video-poster-overlay");
		var iframe = document.querySelector("iframe");
		var player = new Vimeo.Player(iframe);
		poster.classList.add("hidden");
		playIcon.classList.add("hidden");
		playOverlay.classList.add("hidden");
		player.play();
		$(".section17 .video-overlay").addClass("hidden");
	});


}(jQuery));
